import React from 'react'
import img from '../imgs/landing.jpeg'

const Amenity = ({name}) => {
    return ( 
        <div className="transition-colors hover:bg-gray-100 cursor-pointer flex  flex-col justify-center items-center border-gray-100 border m-4 p-4">
           <img className="rounded-full h-20 w-20" src={img} alt="" />
           <p className="m-4 text-center">
               {name}
           </p>
        </div>
     );
}
 
export default Amenity;