import React from 'react'

const ActionButton = ({type}) => {
    const icon  = {
        building:'building',
        pointer: 'hand-pointer',
        image: 'image'
    }
    return ( 
    <div className="text-2xl bg-white w-14 h-14 mb-4 hover:bg-green-100 cursor-pointer flex  justify-center items-center">
        <i class={`fas fa-${icon[type]} text-yellow-600`}></i>
    </div> );
}
 
export default ActionButton;