import React from 'react'
import Amenity from './Amenity';
import styled from '@emotion/styled';
import img from '../imgs/landing.jpeg'
const ImgContainer = styled.div`
    background: url(${img});
    width: 100%;
    background-position: center;
    background-size: cover;
    min-height: 300px;
`

const Amenities = () => {
    const amenities = {
        1: 'City Lights Views',
        2: 'Nice Ilumination',
        3: 'Large Garage',
        4: 'Pet Friendly',
        5: 'Modern Style',
        6: 'Great Place to Live',
        
    }
    return ( 
        <div className="mt-8 grid-flow-row grid grid-cols-5 text-center mb-6">
            <h1 className="text-center mt-10 text-5xl text-gray-700 pl-6">Amenities</h1>
            <div className="options grid col-start-1 col-end-6 grid-cols-2 justify-center md:grid-cols-3  md:col-end-4">
            {Object.keys(amenities).map((key)=>{
                return <Amenity name={amenities[key]}/>
            })}
            </div>
            <ImgContainer className=" col-start-1 col-end-6 md:col-start-4 md:col-end-6">
                
            </ImgContainer>
        </div>

            
     );
}
 
export default Amenities;