import React from 'react'
import img from '../imgs/landing.jpeg'
import styled from '@emotion/styled';
// 7 cols 5 rows

const GalleryContainer = styled.div`
padding: 0 2rem;
    img{
        margin-top:2rem;
        @media only screen and (min-width: 768px) {
        margin-top:0rem;
        object-fit: cover;
        max-width: 100%;
        }

    }
`
const DownImage = styled.div`

    grid-column: 3/6;
    grid-row: 4/6;
    background: url(${img});
    background-position: center;
    background-size: cover;

`
const Gallery = () => {
    return (  
        <GalleryContainer className="md:grid md:grid-rows-5 md:grid-cols-7 md:gap-4">
            <img className="col-start-1 row-start-2 col-end-3 row-end-4" src={img} alt="" />
            <img className="col-start-3 row-start-1 col-end-6 row-end-4" src={img} alt="" />
            <img className="col-start-6 row-start-2 col-end-8 row-end-4" src={img} alt="" />
            <img className="col-start-2 row-start-4 col-end-3 row-end-5" src={img} alt="" />
            <DownImage className="down_img" src={img} alt="" />
            <img className="col-start-6 row-start-4 col-end-7 row-end-6" src={img} alt="" />
        </GalleryContainer>
     );
}
 
export default Gallery;